import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			show: false,
			childInfo: '',
			count:0
		}
	},
	methods: {
		leftClick() {
			uni.switchTab({
				url: "/pages/tabar/user"
			})
		},
		rightClick() {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0
			}, 1000)
			uni.navigateTo({
				url: "/pages/user/accountadd"
			})
		},
		editPage(data) {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0
			}, 1000)
			uni.navigateTo({ 
				url: "/pages/user/accountedit?select_zid=" + data.zid
			})
		},
		childInfoa() {
			var that = this
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("childInfo", data, (res) => {
				if (res.data.code == 200) {
					if (res.data.data != '') {
						that.childInfo = res.data.data;
						that.show = false;
					} else {
						that.show = true;
					}
				}
			})
		},
	},
	onShow() {
		var that = this;
		that.childInfoa()
	}
}
